@import 'colors';

:local(.friends) {
  width: 85%;
  max-width: 500px;
  margin: 0 auto;

  h1 {
    font-size: 16px;
    margin: .25em;
    color: $header;
  }

  h3 {
    font-weight: 500;
    font-size: 16px;
    margin: .5em 0;
    
    a {
      text-decoration: none;
      color: $text;
      
      &:hover {
        color: $accent;
      }
    }
  }

  .button {
    margin: .25em;
    border: none;
    min-width: 64px;
    padding: 0 16px;
    background-color: $accent;
    height: 32px;
    font-size: 13px;
    font-weight: 500;
    border-radius: 2px;
    box-shadow: 0 2px 4px rgba(0,0,0,.25);
    color: white;
    cursor: pointer;
    outline: none;

    &:hover {
      box-shadow: 0 2px 6px rgba(0,0,0,.5);
    }
  }

  .flat-button {
    font-size: 13px;
    font-weight: 500;
    height: 32px;
    color: $accent;
    padding: 0 16px;
    border: none;
    border-radius: 2px;
    background-color: white;
    outline: none;
    cursor: pointer;

    &:hover {
      color: $text;
    }
  }

  .heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .friend-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid $light-blue;
  }
  
  @media screen and (min-width: 550px) {
    h1 {
      font-size: 18px;
    }

    h3 {
      margin: .75em 0;
    }

    .button, .flat-button {
      font-size: 14px;
      height: 36px;
    }

    .heading {
      margin: .75em 0;
    }
  }
}


